<style>

  #chevron {
      position: relative;
      text-align: center;
      margin-top: -30px;
      padding: 12px;
      margin-bottom: 6px;
      height: 60px;
      width: 100%;
    }
    #chevron:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 51%;
      background: white;
      transform: skew(0deg, 6deg);
    }
    #chevron:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 50%;
      background: white;
      transform: skew(0deg, -6deg);
    }
</style>